<style scoped>
	.v-data-table >>> .v-data-table-header th {
		text-align: center !important;
	}
    #loading_img{
        position: relative;
        top:32%;
        left:43%;
    }
</style>
<style>
	.swal2-title{
		font-size:22px !important;
	}
</style>
<template>
	<v-layout wrap row>
		<v-flex xs12 sm12 md12 style="margin-bottom:20px;">
			<v-card xs12 sm12 md12>
				<v-card-title xs12 sm12 md12 class="fontDefault">
					{{ this.$router.app.$route.query.screen_name }}
				</v-card-title>
			</v-card>
		</v-flex>
		<v-flex xs12 sm12 md12>
			<v-card>
				<v-card-text>
					<template v-if="this.save_yn=='N'">
						<v-row>
							<v-col xl="2" md="2" sm="2" cols="2" align-self="center" style="padding-bottom:0px;margin-bottom:0px;">
								<v-select 
									v-model="schVal2" 
									:items="clm_use_items" 
									item-text="text"
									item-value="value"
									open-on-clear 
									persistent-hint
									@change="getList()"
									label="사용여부">
								</v-select>
							</v-col>
							<v-col xl="10" md="10" sm="10" cols="10" align-self="center" style="padding-bottom:0px;margin-bottom:0px;">
								<v-text-field v-model="schVal" class="fontDefault" label="검색" single-line @keypress.enter.prevent="getList"></v-text-field>
							</v-col>
						</v-row>
						<v-row class="justify-end">
							<template v-if="this.$router.app.$route.query.update_yn=='Y'">
								<v-col xl="1" md="1" sm="2" cols="6" align-self="center" style="padding-top:0px;margin-top:0px;">
									<v-btn width="100%" color="green" v-on:click="Regopen" style="color:white;">등록</v-btn>
								</v-col>
							</template>
							<!-- <v-col xl="1" md="1" sm="2" cols="3" align-self="center" style="padding-top:0px;margin-top:0px;">
								<v-btn width="100%" color="primary" v-on:click="getExcel" style="color:white;">엑셀</v-btn>
							</v-col>
							<v-col xl="1" md="1" sm="2" cols="3" align-self="center"  style="padding-top:0px;margin-top:0px;">
								<v-btn width="100%" color="purple" style="color:white;" v-on:click="ExcelUploadModal">엑셀등록</v-btn>
							</v-col> -->
							<v-col xl="1" md="1" sm="2" cols="6" align-self="center" style="padding-top:0px;margin-top:0px;">
								<v-btn width="100%" color="white" v-on:click="getList">검색</v-btn>
							</v-col>
						</v-row>
					</template>
					<template v-else>
						<v-row>
							<v-col xl="2" md="2" sm="2" cols="2" align-self="center" style="padding-bottom:0px;margin-bottom:0px;">
								<v-select 
									v-model="schVal2" 
									:items="clm_use_items" 
									item-text="text"
									item-value="value"
									open-on-clear 
									persistent-hint
									disabled
									label="사용여부">
								</v-select>
							</v-col>
							<v-col xl="10" md="10" sm="10" cols="10" align-self="center" style="padding-bottom:0px;margin-bottom:0px;">
								<v-text-field v-model="schVal" class="fontDefault" label="검색" single-line disabled></v-text-field>
							</v-col>
						</v-row>
						<v-row class="justify-end">
							<template v-if="this.$router.app.$route.query.update_yn=='Y'">
								<v-col xl="1" md="1" sm="2" cols="6" align-self="center" style="padding-top:0px;margin-top:0px;">
									<v-btn width="100%" color="orange" disabled style="color:white;">등록</v-btn>
								</v-col>
							</template>
							<!-- <v-col xl="1" md="1" sm="2" cols="3" align-self="center" style="padding-top:0px;margin-top:0px;">
								<v-btn width="100%" color="primary" disabled style="color:white;">엑셀</v-btn>
							</v-col>
							<v-col xl="1" md="1" sm="2" cols="3" align-self="center"  style="padding-top:0px;margin-top:0px;">
								<v-btn width="100%" color="purple" disabled style="color:white;">엑셀등록</v-btn>
							</v-col> -->
							<v-col xl="1" md="1" sm="2" cols="6" align-self="center" style="padding-top:0px;margin-top:0px;">
								<v-btn width="100%" color="primary" disabled>검색</v-btn>
							</v-col>
						</v-row>
					</template>
					<v-row>
						<template v-if="this.save_yn=='N'">
							<v-col>
								<v-data-table class="elevation-1" :footer-props="{ 'items-per-page-text': '페이지 당 행',  pageText: '총 {2} 항목 중 {0}-{1}'}"  @click:row="onClickRow" :headers="headers" :items="document" @pagination="updatePage" :itemsPerPage="list_page" :loading="loading" >
									<template slot="items" slot-scope="props">
										<td>{{ props.item.num }}</td>
										<td>{{ props.item.clm_employee_number }}</td>
										<td>{{ props.item.clm_user_id }}</td>
										<td>{{ props.item.clm_user_name }}</td>
										<td>{{ props.item.clm_user_department_id }}</td>
										<td>{{ props.item.clm_user_authority_id }}</td>
										<td>{{ props.item.clm_comment }}</td>
									</template>
								</v-data-table>
							</v-col>
						</template>
						<template v-else>
							<img style="padding-top:200px;padding-bottom:200px;" id="loading_img" src="product/loading.gif" />
						</template>
					</v-row>
					<v-row justify="center">
						<v-dialog v-model="regmodal" persistent max-width="950" @keydown.esc="regmodal=false">
							<v-card>
							<template v-if="this.num == ''">
							<v-toolbar class="fontDefault" color="light-blue" dark>사용자 등록</v-toolbar>
							</template>
							<template v-if="this.num != ''">
							<v-toolbar class="fontDefault" color="light-blue" dark>사용자 수정</v-toolbar>
							</template>
							<v-card-text style="padding-bottom:0px;">
								<v-row>
									<v-col xl="3" md="3" sm="3" cols="3" style="padding-bottom:0px;margin-bottom:0px;padding-top:25px;margin-top:0px;">
										<template v-if="this.num == ''">
										<v-text-field label="ID" v-model="clm_user_id" />
										</template>
										<template v-if="this.num != ''">
										<v-text-field label="ID" v-model="clm_user_id" disabled />
										</template>
										<input type="hidden" id="num" />
									</v-col>
									<v-col xl="3" md="3" sm="3" cols="3" style="padding-bottom:0px;margin-bottom:0px;padding-top:25px;margin-top:0px;">
										<v-text-field type="password" label="비밀번호" v-model="clm_user_pw" />
									</v-col>
									<v-col xl="3" md="3" sm="3" cols="3" style="padding-bottom:0px;margin-bottom:0px;padding-top:25px;margin-top:0px;">
										<v-text-field type="password" label="비밀번호 확인" v-model="clm_user_pw_confirm" />
									</v-col>
									<v-col xl="3" md="3" sm="3" cols="3" style="padding-bottom:0px;margin-bottom:0px;padding-top:25px;margin-top:0px;">					
										<v-select 
											v-model="clm_use_yn" 
											:items="clm_use_yn_items" 
											item-text="text"
											item-value="value"
											open-on-clear 
											persistent-hint
											label="사용여부">
										</v-select>
									</v-col>
								</v-row>
								<v-row>
									<v-col xl="3" md="3" sm="3" cols="3" style="padding-bottom:0px;margin-bottom:0px;padding-top:0px;margin-top:0px;">
										<v-text-field label="이름" v-model="clm_user_name" />
									</v-col>
									<v-col xl="3" md="3" sm="3" cols="3" style="padding-bottom:0px;margin-bottom:0px;padding-top:0px;margin-top:0px;">
										<template v-if="this.num == ''">
											<v-text-field label="사원번호" v-model="clm_employee_number" type="number" />
										</template>
										<template v-if="this.num != ''">
											<v-text-field label="사원번호" v-model="clm_employee_number" readonly />
										</template>
									</v-col>
									<v-col xl="3" md="3" sm="3" cols="3" style="padding-bottom:0px;margin-bottom:0px;padding-top:0px;margin-top:0px;">
										<v-select 
										v-model="clm_user_department_id" 
										:items="clm_user_department_id_items" 
										item-text="clm_department_name"
										item-value="clm_department_id"
										open-on-clear 
										persistent-hint
										label="부서">
										</v-select>
									</v-col>
									<v-col xl="3" md="3" sm="3" cols="3" style="padding-bottom:0px;margin-bottom:0px;padding-top:0px;margin-top:0px;">
										<v-select 
										v-model="clm_user_authority_id" 
										:items="clm_user_authority_id_items" 
										item-text="clm_authority_name"
										item-value="clm_authority_id"
										open-on-clear 
										persistent-hint
										label="권한">
										</v-select>
									</v-col>
								</v-row>
								<v-row>
									<v-col xl="12" md="12" sm="12" cols="12" style="padding-bottom:0px;margin-bottom:0px;padding-top:0px;margin-top:0px;">
										<v-text-field label="비고" v-model="clm_comment" />
									</v-col>
								</v-row>
							</v-card-text>
							<v-card-actions class="justify-end">
								<template v-if="this.num == '' && this.$router.app.$route.query.update_yn=='Y'">
								<v-btn text @click="Save" >등록</v-btn>
								</template>
								<template v-if="this.num != '' && this.$router.app.$route.query.update_yn=='Y' ">
								<v-btn text @click="Update" >수정</v-btn>
								</template>
								<!-- <template v-if="this.num != '' && this.$router.app.$route.query.delete_yn=='Y'">
									<v-btn text @click="Del">삭제</v-btn>
								</template> -->
								<v-btn text @click="regmodal = !regmodal">닫기</v-btn>
							</v-card-actions>
							</v-card>
						</v-dialog>
					</v-row>
					<v-row justify="center">
						<v-dialog v-model="ExcelModal" persistent max-width="700" @keydown.esc="ExcelModal = !ExcelModal">
							<v-card>
								<v-toolbar class="fontDefault" color="light-blue" dark>엑셀등록</v-toolbar>
								<v-card-text style="padding-bottom:0px;">
									<v-row>
										<v-col xl="12" md="12" sm="12" cols="12"  align-self="center" style="padding-bottom:0px;margin-bottom:0px;padding-top:25px;">
											<v-file-input accept=" .xls, .xlsx" prepend-icon="mdi-folder-upload" counter show-size outlined small-chips v-model="excel_upload_file" ref="excel_upload_file"></v-file-input>										
										</v-col>
									</v-row>
								</v-card-text>
								<v-card-actions class="justify-end">
									<v-btn text @click="ExcelUpload">엑셀 업로드</v-btn>
									<v-btn text @click="ExcelModal = !ExcelModal">닫기</v-btn>
								</v-card-actions>
							</v-card>
						</v-dialog>
					</v-row>					
				</v-card-text>
			</v-card>
		</v-flex>
	</v-layout>
</template>

<script src="//cdn.jsdelivr.net/npm/sweetalert2@11"></script>
<script>
import axios from 'axios';
import Swal from 'sweetalert2'

export default {
	data() {
		return {
			headers: [
				{ text: '순번', align: 'end', value: 'num', width: 80 },
				{ text: '사원번호', align: 'center', value: 'clm_employee_number', width: 150 },
				{ text: '사용자ID', align: 'center', value: 'clm_user_id', width: 150 },
				{ text: '사용자이름', align: 'center', value: 'clm_user_name', width: 200 },
				{ text: '사용여부', align: 'center', value: 'clm_use_gubun', width: 150 },
				{ text: '부서명', align: 'center', value: 'clm_department_name', width: 200 },
				{ text: '권한', align: 'center', value: 'clm_authority_name', width: 200 },
				{ text: '비고', align: 'center', value: 'clm_comment', width: 250 },
			],
			document: [],
			options: {
				multiSort: false,
				sortBy: [],
				sortDesc: [],
				page: 1,
				itemsPerPage: 5,
			},
			footerOptions: {
				'items-per-page-options': [5, 10, 25, 50, 100],
			},
			totalCount: 0,
			loading: false,
			regmodal:false,
			schVal: '',
			clm_user_id: '',
			clm_user_name: '',
			num:'',
			clm_user_pw: '',
			clm_employee_number:'',
			clm_user_pw_confirm: '',
			clm_user_department_id: '',
			clm_user_department_id_items: [],
			clm_user_authority_id: '',
			clm_user_authority_id_items: [],
			clm_comment: '',
			save_yn:'N',
			list_page:10,
			loading_yn:'N',
			schVal2:'N',
			clm_use_items: [
				{ text:"전체",value:"" },
				{ text:"사용",value:"N" },
				{ text:"미사용",value:"Y" }
			],
			clm_use_yn_items: [
				{ text:"Y",value:"Y" },
				{ text:"N",value:"N" }
			],
			clm_use_yn: 'Y',
			clm_origin_use_yn: 'Y',
			/* Excel Modal */
			ExcelModal: false,
			excel_upload_file: '',
		}
	},
	mounted() {
		this.schVal2='N';
		axios.get('/back/Common/DepartmentList')
			.then(response => {
				this.clm_user_department_id_items = response.data;
			})
			.catch(error => {
				console.log(error)
			})
		axios.get('/back/Common/AuthorityList')
			.then(response => {
				this.clm_user_authority_id_items = response.data;
			})
			.catch(error => {
				console.log(error)
			}),
		this.getList()
	},
	watch: {
		options: {
			handler() {
				this.getList()
			},
			deep: true,
		},
	},
	methods: {
		updatePage(page){
			this.list_page=Number(page.itemsPerPage);
		}, 
		ExcelUploadModal(){
			this.excel_upload_file = '';
			this.ExcelModal = true;
		},
		ExcelUpload(){
			if(this.loading_yn == 'Y'){
				Swal.fire({
					title: '저장중입니다.잠시만 기다려주세요.',
					icon: 'error'
				});
				return;
			}

			if(this.excel_upload_file == null || this.excel_upload_file == ""){
				Swal.fire({
					title: '등록된 엑셀파일이 없습니다.',
					icon: 'error'
				});
				return;
			}else{
				this.loading_yn = 'Y';
				const formData = new FormData();
				formData.append("user_id", this.$session.get('user_id'));
				formData.append('excelFile', this.excel_upload_file);

				axios.post('/back/SCR007002/ExcelUpload'
						, formData										
						, { headers: { 'Content-Type': 'multipart/form-data' } 
				}).then(response => {
					if (response.status == '200') {
						var incomeRow = '';
						var ErrorRow = '';
						var UpdateRow = '';
						var ErrorName = '';
						this.loading_yn = 'N';
						if(response.data != '0'){
							var tmp_arr = String(response.data).split("▦");
							incomeRow = String(tmp_arr[0]); // 실제로 등록된 수 
							ErrorRow = String(tmp_arr[1]); // 등록 중 오류 난 수
							UpdateRow = String(tmp_arr[2]); // 업데이트된 수
							ErrorName = String(tmp_arr[3]);
							if(ErrorName == '') {
								ErrorName = '없음';
							}else {
								ErrorName = (ErrorName).substring(1); // 등록 중 오류 난 이름
							}
							Swal.fire({
								title: '정상적으로 엑셀파일이 업로드 되었습니다.\n<엑셀업로드 결과>\n신규등록된 사용자 : ' + incomeRow+'\n수정된 사용자 : ' + UpdateRow +'\n등록 실패한 사용자 : ' + ErrorRow  +'\n등록 실패한 사용자명 : ' + ErrorName,
								icon: 'error'
							});
						}else if(response.data == '0'){
							Swal.fire({
								title: '엑셀파일의 형식이 잘못되었습니다.',
								icon: 'error'
							});
						}

						this.excel_upload_file = '';
						this.ExcelModal = false;
						this.getList();
					}else{
						Swal.fire({
							title: '엑셀파일 업로드를 실패했습니다.',
							icon: 'error'
						});
					}
				}).catch(error => {})	
			}
		},
		Regopen(){
			this.loading_yn='N';
			this.num="",
			this.clm_user_id= "",
			this.clm_user_name = "",
			this.clm_user_pw = "",
			this.clm_user_pw_confirm = "",
			this.clm_user_department_id = "",
			this.clm_user_authority_id = "",
			this.clm_comment = "",
			this.clm_employee_number ="",
			this.clm_use_yn ='Y',
			this.clm_origin_use_yn ='Y',
			this.regmodal=true;
		},
		Save() {
			if(this.loading_yn=='Y'){
				Swal.fire({
					title: '저장중입니다.잠시만 기다려주세요.',
					icon: 'error'
				});
				return;
			}
			this.loading_yn='Y';
			if(this.clm_user_id == ''){
				Swal.fire({
					title: 'ID가 빈칸입니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_user_pw != this.clm_user_pw_confirm){
				Swal.fire({
					title: '비밀번호와 비밀번호 확인이 맞지 않습니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;			
			}else if(this.clm_user_pw == ''){
				Swal.fire({
					title: '비밀번호가 빈칸입니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_user_name == ''){
				Swal.fire({
					title: '이름이 빈칸입니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_employee_number == ''){
				Swal.fire({
					title: '사원번호가 빈칸입니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_user_department_id == ''){
				Swal.fire({
					title: '부서를 선택해주시기 바랍니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_user_authority_id == ''){
				Swal.fire({
					title: '권한을 선택해주시기 바랍니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}
			axios.get('/back/SCR007002/CheckUserId',{
				params: { 
					clm_user_id: this.clm_user_id
					}
			}).then(
				response => {
					if(Number(response.data)>0){
						Swal.fire({
							title: '이미 존재하는 사용자 ID입니다.',
							icon: 'error'
						});
						this.loading_yn='N';
						return;
					}else{
						axios.post('/back/SCR007002/Reg', null, { params: {
						clm_user_id: this.clm_user_id,
						clm_user_name: this.clm_user_name,
						clm_user_pw: this.clm_user_pw,
						clm_user_pw_confirm: '',
						clm_user_department_id: this.clm_user_department_id,
						clm_user_authority_id: this.clm_user_authority_id,
						clm_employee_number : this.clm_employee_number,
						clm_comment: this.clm_comment,
						clm_use_yn : this.clm_use_yn,
						clm_user: this.$session.get('user_id')
						}})
						.then(response => {
							if (response.status == '200') {
								this.loading_yn='N';
								Swal.fire({
									title: '정상적으로 등록 되었습니다.',
									icon: 'success'
								});
								this.getList();
							}
						})
						.catch(error => {
							console.log(error)
						})			
					this.regmodal = false;
					this.getList();
					}
			})
		},
		Update() {
			if(this.loading_yn=='Y'){
				Swal.fire({
					title: '저장중입니다.잠시만 기다려주세요.',
					icon: 'error'
				});
				return;
			}
			this.loading_yn='Y';
			if(this.clm_user_id == ''){
				Swal.fire({
					title: 'ID가 빈칸입니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_user_pw != this.clm_user_pw_confirm){
				Swal.fire({
					title: '비밀번호와 비밀번호 확인이 맞지 않습니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;			
			}else if(this.clm_user_pw == ''){
				Swal.fire({
					title: '비밀번호가 빈칸입니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_user_name == ''){
				Swal.fire({
					title: '이름이 빈칸입니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_employee_number == ''){
				Swal.fire({
					title: '사원번호가 빈칸입니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_user_department_id == ''){
				Swal.fire({
					title: '부서를 선택해주시기 바랍니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}else if(this.clm_user_authority_id == ''){
				Swal.fire({
					title: '권한을 선택해주시기 바랍니다.',
					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}
			axios.post('/back/SCR007002/Update', null, { params: {
					clm_user_id: this.clm_user_id,
					clm_user_name: this.clm_user_name,
					clm_user_pw: this.clm_user_pw,
					clm_user_pw_confirm: '',
					clm_user_department_id: this.clm_user_department_id,
					clm_user_authority_id: this.clm_user_authority_id,
					clm_employee_number : this.clm_employee_number,
					clm_comment: this.clm_comment,
					clm_use_yn : this.clm_use_yn,
					clm_origin_use_yn : this.clm_origin_use_yn,
					clm_user: this.$session.get('user_id')
				}})
				.then(response => {
					if (response.status == '200') {
						this.loading_yn='N';
						Swal.fire({
							title: '정상적으로 수정 되었습니다.',
							icon: 'success'
						});
						this.getList();
					}
				})
				.catch(error => {
					console.log(error)
				})
				this.regmodal = false;
			this.getList();
		},
		getList() {
			const vm = this
			this.loading = true
			this.save_yn='Y';
			
			return new Promise((resolve, reject) => {
				const { sortBy, sortDesc, page, itemsPerPage } = this.options
				let sort = []
				let TsortBy = ''
				let TsortDesc = ''
				if (sortBy.length > 0) {
					// eslint-disable-next-line
					sortBy.forEach((value, index) => {
						TsortBy = value
						if (sortDesc[index]){
							TsortDesc = 'desc'
						}else{
							TsortDesc = 'asc'
						}
					})
				} else {
					TsortBy = 'cast(clm_employee_number as integer)'
					TsortDesc = 'desc'
					
				}
				let items = axios.get('/back/SCR001003/List',{
						params: {
						sortBy: TsortBy,
						sortDesc: TsortDesc,
						search: this.schVal,
						search2: this.schVal2,
						page: page,
						PerPage: itemsPerPage
				        }
					}).then(
					response => {
						items = response.data
						setTimeout(() => {
							vm.loading = false
							this.save_yn='N';
							this.document = items
						}, 1000)
					},
				)
			})
		},
		getExcel(){
			var date = new Date();
			var year = date.getFullYear();
			var month = (date.getMonth() + 1);
			var day = date.getDate();
			var hour = date.getHours();
			var min = date.getMinutes();
			var sec = date.getSeconds();

			month = (month < 10) ? "0" + String(month) : month;
			day = (day < 10) ? "0" + String(day) : day;
			hour = (hour < 10) ? "0" + String(hour) : hour;
			min = (min < 10) ? "0" + String(min) : min;
			sec = (sec < 10) ? "0" + String(sec) : sec;

			var filename = this.$router.app.$route.query.screen_name + '-' + year + month + day + hour + min + sec + '.xlsx';

			const vm = this
			this.loading = true
			this.save_yn='Y';
			
			return new Promise((resolve, reject) => {
				const { sortBy, sortDesc, page, itemsPerPage } = this.options
				let sort = []
				let TsortBy = ''
				let TsortDesc = ''
				if (sortBy.length > 0) {
					// eslint-disable-next-line
					sortBy.forEach((value, index) => {
						TsortBy = value
						if (sortDesc[index]){
							TsortDesc = 'desc'
						}else{
							TsortDesc = 'asc'
						}
					})
				} else {
					TsortBy = 'cast(clm_employee_number as integer)'
					TsortDesc = 'desc'
					
				}
				let items = axios.get('/back/SCR007002/Excel',{
						params: {
							sortBy: TsortBy,
							sortDesc: TsortDesc,
							search: this.schVal,
							search2: this.schVal2,
							excel_col_eng: 'num▦clm_employee_number▦clm_user_id▦clm_user_name▦clm_department_name▦clm_authority_name▦clm_comment',
							excel_col_kor: '순번▦사원번호▦사용자ID▦사용자이름▦부서명▦권한▦비고',
							excel_col_align: 'R▦C▦C▦C▦C▦C▦L',
							excel_col_length: '1200▦3200▦3200▦3200▦3200▦3200▦7000',
							screen_name: this.$router.app.$route.query.screen_name,
							filename: filename,
							page: page,
							PerPage: itemsPerPage
						},
						responseType: "blob"
					}).then(
					response => {
						const url = window.URL.createObjectURL(new Blob([response.data]));
						const link = document.createElement("a");
						link.href = url;
						link.setAttribute("download", filename);
						document.body.appendChild(link);
						link.click();
						vm.loading = false
						this.save_yn='N';
					},
				)
			})
		},
		onClickRow(event, data){
			this.num = data.item.num,
			this.clm_user_id = data.item.clm_user_id,
			this.clm_user_name = data.item.clm_user_name,
			this.clm_user_pw = data.item.clm_user_pw,
			this.clm_employee_number = data.item.clm_employee_number,
			this.clm_user_pw_confirm = '',
			this.clm_user_department_id = data.item.clm_department_id,
			this.clm_user_authority_id = data.item.clm_authority_id,
			this.clm_comment = data.item.clm_comment,
			this.clm_use_yn= data.item.clm_use_yn;
			this.clm_origin_use_yn= data.item.clm_use_yn;
			this.regmodal=true;
		},
		// onClickRow(event, data) {
		// 	this.$router.push({ name: 'scr007002View', query: { clm_user_id: data.item.clm_user_id }  })
		// },
		onClickWrite(event, data) {
			this.$router.push({ name: 'scr007002Reg' })
		},
	},
}
</script>